import { Component, h, Prop } from '@stencil/core';

export type PlaceholderSize = 'xs' | 'sm' | 'md' | 'lg';

const placeholderVariants = {
    iconSizes: {
        xs: 'text-[24px] w-24 h-24',
        sm: 'text-[48px] w-48 h-48',
        md: 'text-[64px] w-64 h-64',
        lg: 'text-[72px] w-72 h-72',
    },
    sizes: {
        xs: 'p-16 flex-row gap-8',
        sm: 'p-[5vh] flex-col justify-center',
        md: 'p-[10vh] flex-col justify-center',
        lg: 'p-[15vh] flex-col justify-center',
    },
};

/**
 * @category Feedback
 * @description A placeholder component for displaying empty states, skeleton loading, or temporary content. Useful for indicating missing data or content that is still loading.
 *
 * @slot - Placed below the content
 */
@Component({
    tag: 'at-placeholder',
    shadow: false,
})
export class AtPlaceholderComponent {
    /**
     * Size of the placeholder
     */
    @Prop()
    size: PlaceholderSize = 'md';

    /**
     * Material icon to be displayed in the center of the placeholder
     */
    @Prop()
    icon?: string;

    /**
     * Title to be displayed below the icon
     */
    @Prop()
    placeholder_title: string;

    /**
     * Content to be displayed below the title
     */
    @Prop()
    content: string;

    /**
     * Will show a loading spinner when set
     */
    @Prop()
    show_loading_spinner: boolean;

    get placeholderSizeClass() {
        return placeholderVariants.sizes[this.size];
    }

    get iconSizeClass() {
        return placeholderVariants.iconSizes[this.size];
    }

    render() {
        return (
            <div
                class={`${this.placeholderSizeClass} bg-surface-0 text-med flex w-full items-center gap-16 rounded-sm text-center`}
                data-name="placeholder-container"
            >
                {this.icon && this.icon !== 'none' && (
                    <span
                        class={`material-icons text-slate-300 !${this.iconSizeClass}`}
                        data-name="placeholder-icon"
                    >
                        {this.icon}
                    </span>
                )}

                <div
                    class={`border-radius-sm flex flex-col justify-center ${this.size === 'xs' ? 'items-start text-left' : 'items-center text-center'}`}
                >
                    <div class="flex items-center">
                        {this.show_loading_spinner && (
                            <at-loading
                                class="relative mr-8"
                                size="sm"
                                type="secondary"
                                data-name="placeholder-spinner"
                            />
                        )}
                        {this.placeholder_title && (
                            <h5
                                class="text-med text-sm font-medium"
                                data-name="placeholder-title"
                            >
                                {this.placeholder_title}
                            </h5>
                        )}
                    </div>
                    <p
                        class="text-med text-sm"
                        data-name="placeholder-content"
                    >
                        {this.content}
                    </p>

                    <slot />
                </div>
            </div>
        );
    }
}
